// src/app/page.tsx
"use client";

import { ResizableHandle, ResizablePanel, ResizablePanelGroup } from "@/components/ui/resizable";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Skeleton } from "@/components/ui/skeleton";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { useState, useEffect } from "react";
import { Terminal } from "lucide-react";

type Estacao = {
  cod_estacao: number;
  estacao: string;
  descricao: string;
};

type Ponto = {
  nponto: number;
  id: string;
  traducao_id: string;
  cod_origem: number;
};

export default function HomePage() {
  const [estacoes, setEstacoes] = useState<Estacao[]>([]);
  const [estacaoSelecionada, setEstacaoSelecionada] = useState<Estacao | null>(null);
  const [pontos, setPontos] = useState<Ponto[]>([]);
  const [loadingEstacoes, setLoadingEstacoes] = useState(true);
  const [loadingPontos, setLoadingPontos] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchEstacoes() {
      try {
        const response = await fetch('/api/estacoes');
        if (!response.ok) throw new Error('Falha ao carregar estações.');
        const data: Estacao[] = await response.json();
        setEstacoes(data);
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Erro desconhecido.');
      } finally {
        setLoadingEstacoes(false);
      }
    }
    fetchEstacoes();
  }, []);

  const handleEstacaoClick = async (estacao: Estacao) => {
    setEstacaoSelecionada(estacao);
    setLoadingPontos(true);
    setPontos([]);
    try {
      const response = await fetch(`/api/pontos/${estacao.cod_estacao}`);
      if (!response.ok) throw new Error('Falha ao carregar pontos.');
      const data: Ponto[] = await response.json();
      setPontos(data);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Erro desconhecido.');
    } finally {
      setLoadingPontos(false);
    }
  };

  return (
    <div className="h-screen bg-gray-50 dark:bg-gray-900">
      <ResizablePanelGroup direction="horizontal" className="h-full max-h-screen">
        <ResizablePanel defaultSize={20} minSize={15} maxSize={30}>
          <div className="flex h-full flex-col p-4">
            <h2 className="text-lg font-semibold mb-2">Subestações</h2>
            <ScrollArea className="flex-grow">
              {loadingEstacoes && <Skeleton className="h-10 w-full my-1" count={10} />}
              {error && <p className="text-red-500">{error}</p>}
              <ul className="space-y-1">
                {estacoes.map((estacao) => (
                  <li
                    key={estacao.cod_estacao}
                    onClick={() => handleEstacaoClick(estacao)}
                    className={`p-2 rounded-md cursor-pointer text-sm font-medium transition-colors ${
                      estacaoSelecionada?.cod_estacao === estacao.cod_estacao
                        ? 'bg-blue-600 text-white'
                        : 'hover:bg-gray-200 dark:hover:bg-gray-700'
                    }`}
                  >
                    <strong className="font-bold">{estacao.estacao}</strong>: {estacao.descricao}
                  </li>
                ))}
              </ul>
            </ScrollArea>
          </div>
        </ResizablePanel>
        <ResizableHandle withHandle />
        <ResizablePanel defaultSize={80}>
          <div className="flex h-full flex-col p-6">
            {estacaoSelecionada ? (
              <>
                <h1 className="text-2xl font-bold">{estacaoSelecionada.descricao}</h1>
                <p className="text-sm text-gray-500 mb-4">
                  Código: {estacaoSelecionada.cod_estacao} | Estação: {estacaoSelecionada.estacao}
                </p>
                <div className="flex-grow overflow-auto">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>ID do Ponto</TableHead>
                        <TableHead>Descrição</TableHead>
                        <TableHead className="text-right">NPonto</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {loadingPontos ? (
                        Array.from({ length: 10 }).map((_, i) => (
                          <TableRow key={i}>
                            <TableCell><Skeleton className="h-5 w-48" /></TableCell>
                            <TableCell><Skeleton className="h-5 w-full" /></TableCell>
                            <TableCell><Skeleton className="h-5 w-20 float-right" /></TableCell>
                          </TableRow>
                        ))
                      ) : (
                        pontos.map((ponto) => (
                          <TableRow key={ponto.nponto}>
                            <TableCell className="font-mono">{ponto.id}</TableCell>
                            <TableCell>{ponto.traducao_id}</TableCell>
                            <TableCell className="text-right font-mono">{ponto.nponto}</TableCell>
                          </TableRow>
                        ))
                      )}
                    </TableBody>
                  </Table>
                </div>
              </>
            ) : (
              <div className="flex h-full items-center justify-center">
                 <Alert className="max-w-md">
                    <Terminal className="h-4 w-4" />
                    <AlertTitle>Nenhuma subestação selecionada</AlertTitle>
                    <AlertDescription>
                      Selecione uma subestação na barra lateral para visualizar seus pontos.
                    </AlertDescription>
                  </Alert>
              </div>
            )}
          </div>
        </ResizablePanel>
      </ResizablePanelGroup>
    </div>
  );
}

